let collections=[];
function choucang(state=collections,action){
    if(action.type==='ADD_COLLECTION'){
        return [...state,action.data]        
    }
    if(action.type==='DELETE_COLLECTION'){
        let arr=[...state];
        arr.splice(action.index,1);
        return arr;
    }
    else{
        return state;
    }
   
}
export default choucang;
