import { SearchBar } from 'antd-mobile'
import { HashRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import React from 'react'
import { Carousel, WingBlank } from 'antd-mobile';
import '../taobao/Home.css'
import { NavBar, Icon } from 'antd-mobile';
import List from './List'
import { render } from 'react-dom';
import Input from 'antd-mobile/lib/input-item/Input';
import SearchBarExample from './SearchBarExample';
// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>

class Home extends React.Component {
  state = {
    data: ['1', '2', '3'],
    imgHeight: 176,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['https://gw.alicdn.com/imgextra/i4/30/O1CN01cXlO9a1C5mVsCi6LN_!!30-0-lubanu.jpg', 'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i4/6000000003837/O1CN016az1BO1eDOKoVKRJ4_!!6000000003837-0-octopus.jpg', 'https://gw.alicdn.com/imgextra/i1/18/O1CN01Kvqdhb1C0HlCij6zr_!!18-0-ppp-picassogw.jpg'],
      });
    }, 1000);
  }
  render() {
    return (
      <div style={{backgroundColor:"rgb(242,242,242)"}}>
         <div>
            <div className="topBox">
              <span className="spanTop">淘</span>
              <div style={{backgroundColor:"rgb(255,133,42)",paddingTop:"5px"}}>
              <NavBar
                mode="light"
                onClick={()=>{this.props.history.push('/searchbarexample')}} 
                icon={ <Icon key="0" type="search" style={{ marginLeft:'50px' ,color:"white"}} />}
                style={{backgroundColor:"rgb(255,78,34)",width:"85%",height:"30px",borderRadius:"30px"}}
              ><span style={{color:"white",textAlign:'center'}}> 搜索宝贝店铺</span></NavBar>
            </div>
            </div>
            <Carousel
          autoplay={true}
          infinite
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.calipay.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={`${val}`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))} 
        </Carousel>
        <List />
        </div>
      </div>
    );
  }
}

export default Home



