import React, { Component } from 'react'
import {withRouter} from 'react-router-dom';
import './Home.css'
import {connect} from 'react-redux'
import {goodslist} from './actionCreater'

const List=(props)=>{
    props.dispatch(goodslist());
    
    return <ul className='goodslist'>
        {
            props.data.map((item)=>{
                return(
                 <li className="imgList"   onClick={()=>{
                    props.history.push({
                     pathname:'/home/detail',
                     img:item.img,
                     title:item.title,
                     id:item.id
             })}} ><img src={item.img}/>
                 <p style={{fontSize:'10px',width:'100%'}}>{item.title}</p>
                 <span style={{fontSize:'15px',color:'red'}}>{item.price}￥</span>
                </li>
                )
            })
        }
        </ul>
}

