// import React from 'react'
import {NavBar,Icon, TabBar} from 'antd-mobile'
import React, { Component } from 'react'
import 'antd-mobile/dist/antd-mobile.css';
import {connect} from 'react-redux'


class Detail extends Component {
    constructor(props){
        super(props);
        this.state={
            selectedTab:'shop'
        }
        
    }
    // componentDidMount(){
    //     this.props.value.map((item)=>{
    //         if(this.props.location.id=item.id){
    //             this.setState({
    //                 selectedTab:'red'
    //             })
    //         }
    //     })
    // }
    render() {
        
        return (
            <div>
                 <NavBar
                mode="light"
                icon={<Icon type="left" />}
                onLeftClick={() => this.props.history.push('/home')}
                    >商品详情</NavBar>
                <img src={this.props.location.img} style={{width:"100%"}}/>
                <p style={{fontSize:"25px",width:"100%"}}>{this.props.location.title}{this.props.location.id}</p>
                <div style={{ position: 'fixed', width: '100%', bottom: 0 }}>
                <TabBar 
                unselectedTintColor="#949494"
			    tintColor="#33A3F4"  //选中字体颜色
                barTintColor="white"  //背景颜色
                >
                <TabBar.Item
                title="店铺"
                key="shop"
                icon={<i className='iconfont icon-dianpu'></i>}
                data-seed="logId"
                >
                </TabBar.Item>
                <TabBar.Item
                title="客服"
                key="clerk"
                icon={<i className='iconfont icon-kefu'></i>}
                data-seed="logId"
                >
                </TabBar.Item>
                <TabBar.item
                title="收藏"
                selected={this.state.selectedTab === "blue"}
                key="collections"
                icon={<i className='iconfont icon-shoucang'></i>}
                selectedIcon={<i className='iconfont icon-shoucang2'></i>}
                onPress={() => {
                    if(localStorage.getItem('username')){
                        this.props.dispatch({
                            type:'ADD_COLLECTION',
                            data:{img:this.props.location.img,
                                id:this.props.location.id,
                                
                                title:this.props.location.title
                                }

                   })
                   this.setState({
                       selectedTab:"red",
                   });
                }else{
                    this.props.history.push('/logpage')
                }
                    }
                }
                data-seed="logId"
                > 
                </TabBar.item>
                <TabBar.item
                    icon={<button style={{color:'white',width:'100px',height:'35px',background:'#ff6500',border:0,borderRadius:'50px 0 0 50px'}}>加入购物车</button>}
                >
                </TabBar.item>
                <TabBar.item
                    icon={<button style={{color:'white',width:'100px',height:'35px',background:'rgb(255,113,2)',border:0,borderRadius:' 0 50px 50px 0'}}>立即购买</button>}
                ></TabBar.item>
            </TabBar>
            </div>
            </div>
        )
    }
}
const mapStateToProps=(state)=>({
    goods:state.list.data,
    value:state.collections,
    info:state.logpage.username
})
export default connect(mapStateToProps)(Detail);
