import React from 'react'
import { HashRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import provider, { Provider } from 'react-redux'
import SearchBarExample from './SearchBarExample'

import store from './store'

import Collections from './Collections'
import Mytab from './Mytab'
import Home from './Home'
import Tabgoodslist from './Tabgoodslist' 
import My from './My'
import Detail from './Detail'
import Logpage from './Logpage'

import Routes from '../task2/router.config'
import { SearchBar } from 'antd-mobile'

function PrivateRoute({ component:Com, ...rest }) {
	return (
	  <Route
		{...rest}
		render={({ location }) =>
		   localStorage.getItem('username')
		   ?<Com {...rest} /> 
		   : (
			<Redirect
			  to={{
				pathname: "/logpage",
				state: location 
			  }}
			/>
		  )
		}
	  />
	);
  }

const App = () => {
    return (  
        <Provider store={store}>
        <Router>
            <Switch>
                <Route exact path='/' component={Home} />
                <PrivateRoute exact path='/collections' component={Collections} />
                <Route path='/logpage' component={Logpage} />
                <Route path='/tabgoodslist' component={Tabgoodslist} />
                <Route path='/searchbarexample' component={SearchBarExample} />
                <Route path='/my' component={My} />
                <Route path='/home/detail' component={Detail} />
                <Route render ={()=><Redirect to='/' />} />
            </Switch>
            <Mytab  />
        </Router>
        </Provider>
    )
}
export default App;
